.App {
  text-align: center;
}

body {
  margin: 0;
  font-family: 'Bitter', serif;
}


.verticalCenter {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

/* Must be used as the parent */
.horizontalCenter {
  display: flex;
  justify-content: center;
}

.navBarDiv {
  height: 5vh;
  width: 100%;
  margin: 0;
  position: relative;
  background: #D1B1CB;
}

.navBarButton {
  margin: 1.4vh 1vw 0 1vw;
  padding-left: 0.5vw;
  padding-right: 0.5vw;
  display: inline-flex;
  align-items: center;
  float: right;
  }


